.character-items {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  padding-left: 10%;
  padding-right: 10%;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  padding: 50px;
}
.character-items .filter {
  width: 200px;
  padding: 20px;
  border-radius: 10px;
  margin-left: 200px;
  margin-right: 100px;
  box-shadow: 0 0px 12px 0px gray;
}
.character-items .filter p:not(:first-child, :nth-child(2)) {
  cursor: pointer;
}
.character-items .active {
  color: pink;
}
.character-items .separator {
  height: 1;
  background: gray;
}

.character-items .list {
  width: 50%;
}
.character-items .item {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  border: 1px solid white;
  box-shadow: 0 0px 12px 0px gray;
  margin-bottom: 20px;
  border-radius: 20px;
  padding: 10px;
  padding-top: 20px;
}
.character-items .item img {
  width: 120px;
  height: 200px;
  border-radius: 20px;
  margin-left: 40px;
  margin-right: 40px;
}
.character-items .item .values p {
  font-size: 16px;
  font-weight: bold;
  line-height: 20px;
}

