html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  background-image: url("https://unrealkingdoms.com/PlayerLogin/img/bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: white;
  height: 100%;
  font-family: "Numans", sans-serif;
}

form {
  width: 100%;
}
.dashboardBox {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 30%;
  position: relative;
  left: 50%;
  -webkit-transform: translate(-50%, 50%);
          transform: translate(-50%, 50%);
  padding: 20px;
  background-color: #00000068;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.4);
}

.downloadSubmit {
  margin: 25px 0 15px 0;
}

@media screen and (max-width: 550px) {
  .dashboardBox {
    width: 85%;
  }
}

.dashboardBox input::-webkit-input-placeholder {
  font-size: 18px;
  font-family: "Numans", sans-serif;
}

.dashboardBox input:-ms-input-placeholder {
  font-size: 18px;
  font-family: "Numans", sans-serif;
}

.dashboardBox input::placeholder {
  font-size: 18px;
  font-family: "Numans", sans-serif;
}
.dashboardBox input:focus-visible {
  outline: none !important;
}
.dashboardBox-text {
  text-align: center;
  font-family: "Numans", sans-serif;
  font-size: 25px;
}

