.dashboard {
  color: white;
}
.profile-container {
  width: 100%;
  display: flex;
  align-items: center;
}
.profile {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.profile img:first-child {
  width: 400px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.profile img:nth-child(2) {
  width: 100px;
  border-radius: 50px;
  padding: 10px;
  margin-top: -50px;
  background: white;
}
.profile p {
  margin: 10px;
  font-size: 20px;
}
